<template>
    <div   style="height:calc(100% - 1.2em)">
        <div class="info-header">
          <span class="callback" @click="resetComponent">电量电费统计 </span>
          <span> > 设备使用详情</span>
        </div>
        <div class="info-container" style="height:calc(100% - 0em)">
            <div  class="list-content detail-content  list-contentB topInfoCon" >
               <div class="list-content commInfoRs">
                  <div class="list-header " style="border:0">
                    <i class="hasIcon hasImg">
                      <img src="dist/img/widsomApp/basic.png" />
                    </i>基本信息
                  </div>
                  <ul class="form-list" >
                     <li v-for="(item,$index) in commInfo" class="allWidth" style="margin:0!important">
                         <span class="label-span">{{item.text}}</span>
                         <span class="label-span" style="max-width: calc(100% - 8em);"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                     </li>
                  </ul>
              </div>
              <div class="list-content commInfoLs">
                 <div class="list-header " style="border:0">
                   <i class="hasIcon hasImg">
                     <img src="dist/img/widsomApp/data_statistics.png" />
                   </i>数据信息
                 </div>
                 <div class="accountCon">
                   <div class="oneInFours">
                     <div class="contentES ">
                       <div class="analyseTypeS">
                          <div>次数</div>
                          <div class=" bigSp" >{{row.useNumber}}</div>
                          <div>次</div>
                       </div>
                     </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BO">
                         <div  class="analyseTypeS">
                            <div>电费</div>
                            <div class=" bigSp">{{row.electricityFees}}</div>
                            <div>元</div>
                         </div>
                      </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BY">
                        <div  class="analyseTypeS">
                           <div>电量</div>
                           <div class=" bigSp" >{{row.electricQuantity}}</div>
                            <div>KWH</div>
                        </div>
                      </div>
                   </div>
                   <div class="oneInFours">
                      <div class="contentES BG">
                         <div  class="analyseTypeS">
                            <div>盈利</div>
                            <div class=" bigSp" >{{row.profitable}}</div>
                            <div>元</div>
                          </div>
                       </div>
                   </div>
                 </div>
              </div>
            </div>
            <div  class="list-content  list-contentB " style="padding: 0.5em 1.5em;height:calc(100% - 15.5em)" >
              <div class="list-header noborder-ListHeader" >
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/table_list.png" />
                </i>设备使用记录列表
              </div>
              <div class="operate-div">
                  <inputs ref="inputs"  :option="searchInputOpt" @getInput="getFrom" style="margin-right:1.5em"></inputs>
              </div>
              <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 3.8em)" >
                 <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="openDialogs"></tables>
              </div>
            </div>
        </div>
        <dialogs type="customer" ref="detail" width="65em" maxHeight="620px">
            <div slot="header">
                <span>{{title}}</span>
            </div>
            <div slot="content">
                <div class="costAccount-content" v-if="isDialogTable">
                    <div class="mychar-box">
                        <div ref="currentChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                        <div ref="voltageChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                        <div ref="powerChart">
                            <div  class="emptyShowPic">
                                <div class="emptyContent">
                                    <div v-if="isLoading">
                                        <img src="dist/img/empty-data.png" >
                                        <p>查不到相关数据</p>
                                    </div>
                                    <img src="dist/img/loading.gif" v-if="!isLoading" style="width: 5em;height: 5em">
                                </div>
                            </div>
                        </div>
                    </div>
                    <tables ref="caution"  class="dialogTable" :tableOpt="dialogTable"></tables>
                </div>
            </div>
        </dialogs>
    </div>
</template>
<script>
    export default{
        data() {
            return {
               tableShow: false,
               searchParam: {
                  customerGuid: '',
                  chargingPlieGuid: '',
                  startDate: '',
                  endDate: '',
                  deviceNo: ''
               },
               accountData: {
                  useAccount: '--',
                  elecFee: '--',
                  elecAccount: '--',
                  profitAccount: '--'
               },
               searchInputOpt:  {
                   type: 'typeSearch',
                   key: 'searchParam',
                   selectOpt: {
                     width: '7em',
                     cs: 'client',
                     data: [
                      {text: '使用ID', value: 'customerGuid'},
                      {text: '端口号', value: 'port'}
                     ]
                   }
               },
               tableOpt: {
                 column: [
                    {title: '端口号', field: 'port', textAlign: 'center', width: '100'},
                    {title: '类别', field: 'carCategory',textAlign: 'center',width: '50', type: 'string',
                      formatter: function (val,index, item, obj) {
                          return '电瓶车'
                    }},
                    {title: '计费方式', field: 'payCategory', textAlign: 'center', width: '120', type: 'string',
                      formatter: function (val, index, item, obj) {
                        let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                        return obj.$parent.$parent.getEnumItem('payCategory', vals).text
                        
                    }},
                    {title: '充值费用 (元)', field: 'payMoney', textAlign: 'center', width: '130'},
                    {title: '使用电量 (KWH)', field: 'usePower', textAlign: 'center', width: '130'},
                    {title: '盈利费用 (元)', field: 'profitable', textAlign: 'center', width: '130'},
                    {title: '状态', field: 'state',textAlign: 'center',width: '80', type: 'string',
                      formatter: function (val) {
                        let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                        switch(vals) {
                           case '0':
                              return '<div class="red">正在充电</div>'
                           case '1':
                              return '<div class="green">已完成</div>'
                           default:
                              return '<div >--</div>'
                          }
                    }},
                    {title: '充电时长 (分钟)', field: 'useTime', textAlign: 'center', width: '100'},
                    {title: '开始时间', field: 'startTime', textAlign: 'center', width: '140'},
                    {title: '结束时间', field: 'endTime', textAlign: 'center', width: '140'},
                    {title: '使用ID', field: 'customerGuid', textAlign: 'center', width: '200', type: 'string',
                        formatter: function (val, index, item, obj) {
                            // if(item.openMeans === 3){
                            //     return item.openNo
                            // }else{
                            //     return val
                            // }
                            return val;
                        }}
                 ],
                  url: 'charger/deviceDetialUseList',
                  method: 'post',
               //  method: 'get',
               //  url: 'dist/json/deviceUse.json',
                 pageSize: 20,
                 pageNumber: 1,
                 pageList: [20, 40, 60],
                 queryParam: {},
                 isExport: true,
                 exportURL: 'charger/deviceDetialUseDown'
              },
                // 以下为弹框
                lineData: {},
                deviceCommInfo: "",
                lineV: '',
                lineA: '',
                lineR: '',
                isDialogTable: false,
                title: '',
                isLoading: false,
                dialogTable:{
                    column: [
                        {title: '时间', width: '140', field: 'heartTime', textAlign: 'center'},
                        // {title: '端口', width: '120', field: 'port', textAlign: 'center'},
                        {title: '状态', field: 'state', textAlign: 'center',  width: 120,type: 'string',tips: 'eventContent',
                          formatter: function (val, index, row, obj) {
                           let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                              switch(vals) {
                                  case '0':
                                      return `<div class="green">正常</div>`
                                  case '1':
                                  case '-2':
                                  //${row.eventContent}
                                      return `<div class="red">告警</div>`
                                  case '-1':
                                      return `<div class="orange">充电结束</div>`
                                   default: 
                                      return `<div>--</div>`   
                                  }
                            
                          }
                        },
                        {title: '电压', width: '120', field: 'voltage', textAlign: 'center'},
                        {title: '电流', width: '120', field: 'current', textAlign: 'center'},
                        {title: '功率', width: '120', field: 'power', textAlign: 'center'},
                        {title: '电量', width: '120', field: 'activeTotal', textAlign: 'center'},
                        {title: '剩余时间(分钟)', width: '160', field: 'remainTime', textAlign: 'center',type: 'string',formatter: function (val, index, row, obj) {
                                if(typeof val !== 'undefined') {
                                    return val.toString()
                                }
                                return '--'
                            }
                        }
                    ],
                    method: 'post',
                    url: 'charger/queryHeartBychargingGuid',
                    // noFooter: true,
                    queryParam: {},
                    pageSize: 20,
                    pageNumber: 1,
                    height: 250,
                    isExport: true,
                    exportURL: 'charger/downHeartBychargingGuid',
                }
            }
        },
        props: ['row', 'controls', 'searchParams'],
        methods: {
            getFrom: function (val) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                 this.searchParam = this.cleanSearchData(this.searchParam, ['chargingPlieGuid', 'startDate', 'endDate', 'deviceNo']);
                 this.getOption(val, true);
                 this.getParams(1);
               }
            },
            getParams: function (pageNumber) {
               let arr = this.getSearchData(this.searchParam);
               this.getHeight('calc(100% - 3.5em)');
               this.searchTable(arr, pageNumber);
            },
           //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            // getDeviceInfo: function () {
            //   let that = this;
            //   that.getRequest({
            //     url: '',
            //     method: 'post',
            //     param: {chargingPlieGuid: that.row.chargingPlieGuid},
            //     success: function (data) {
            //        for(let key in data.data) {
            //           that.deviceCommInfo[key] = data.data[key]
            //        }
            //     }
            //   })
            // }
            // 以下为增加弹框
            //电压折线图
            getLineC: function () {
                this.lineV  = echarts.init(this.$refs.currentChart);
                this.getLine(this.lineV, ['rgba(22,90,162, 1)', 'rgba(22,90,162, 0.1)'], ['电压', 'V'], 'yVoltageData', 'popup_blue')
            },
            //电流折线图
            getLineV: function () {
                this.lineA  = echarts.init(this.$refs.voltageChart);
                this.getLine(this.lineA, ['rgba(192,64,89, 1)', 'rgba(192,64,89, 0.1)'], ['电流', 'A'], 'yCurrentData', 'popup_red')
            },
            //功率折线图
            getLineR: function () {
                this.lineR = echarts.init(this.$refs.powerChart);
                this.getLine(this.lineR,  ['rgba(184,193,62, 1)', 'rgba(184,193,62, 0.1)'], ['功率', 'W'], 'yPowerData', 'popup_yellow')
            },
            getLine: function (obj, color, typeText, key, img) {
                let option = {
                    toolbox: {
                        show: false,
                        feature: {
                            dataZoom: {
                                yAxisIndex: 'none'
                            },
                            restore: {},
                            saveAsImage: {}
                        }
                    },
                    dataZoom: [
                        {
                            type: 'slider',
                            show: true,
                            xAxisIndex: [0],
                            start: 1,
                            end: 100
                        },
                        {
                            type: 'inside',
                            xAxisIndex: [0],
                            start: 0,
                            end: 100
                        }
                    ],
                    grid: {
                        left: 40,
                        right: 30,
                        bottom:40,
                        top: 50
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['1月' ,'2月', '3月', '4月', '5月', '6月', '7月','8月', '9月', '10月','11月','12月'],
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 0.1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color:'rgba(255,255,255, 1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                type: 'dotted',
                                width: 2
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'

                            }
                        }
                    },
                    yAxis: {
                        name: typeText[0] + ' ( ' + typeText[1] + ' ) ',
                        type: 'value',
                        nameTextStyle: {
                            color: 'rgba(255,255,255, 1)',
                        },
                        axisLabel: {
                            color: 'rgba(255,255,255, 0.1)',
                            textStyle: {
                                color: 'rgba(255,255,255, 1)'
                            }
                        },
                        axisLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)'
                            }
                        },
                        splitLine: {
                            show:true,
                            lineStyle: {
                                color: 'rgba(255,255,255, 0.1)',
                                width: 2,
                                type: 'dotted'
                            }
                        }
                    },
                    tooltip : {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    series:
                        {
                            name: typeText[0],
                            type:'line',
                            smooth:false,
                            symbol: "circle",
                            symbolSize: 8,
                           // sampling: 'max',
                            itemStyle: {
                                normal: {
                                    color: color[0]
                                }
                            },
                            markPoint: {
                                // symbol: "pin",
                                data: [
                                    {
                                        type: 'max',
                                        name: '最大值',
                                        symbol: 'image://dist/img/widsomApp/'+img+'.png',
                                        symbolOffset: [0, '-55%'],
                                        label: {
                                            normal: {
                                                padding: [5,10,15,10],
                                                color: '#fff',
                                                position: 'inside'
                                            }

                                        }
                                    }
                                ]
                            },
                            areaStyle: {
                                normal: {
                                    color:  new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                        offset: 0,
                                        color: color[0]
                                    }, {
                                        offset: 1,
                                        color: color[1]
                                    }])
                                }
                            },
                            data: []
                        }
                };
                option.xAxis.data  = this.lineData.xData;
                option.series.data  = this.lineData[key];
                obj.clear();
                obj.setOption(option, true);
            },
            getLineData: function (row) {
                let that = this;
                that.getRequest({
                    url: 'charger/queryCurveCount',
                    method: 'post',
                    param: {
                        chargingPlieGuid: row.chargingPlieGuid,
                        chargingGuid: row.chargingGuid
                    },
                    success: function (data) {
                        let Data = data.data;
                        that.lineData.xData = Data.xData || [];
                        that.lineData.yPowerData = Data.yPowerData || [];
                        that.lineData.yCurrentData = Data.yCurrentData || [];
                        that.lineData.yVoltageData = Data.yVoltageData || [];
                        that.getLineC();
                        that.getLineV();
                        that.getLineR();
                    },
                    error: function () {
                        that.isLoading = true;
                    }
                }, true)
            },
            openDialogs: function (row) {
                let that = this;
                let state = '';
                switch(row.val.state) {
                    case 0:
                        state = '正在充电';
                        break;
                    case 1:
                        state = '已完成';
                        break;
                    default:
                        state =  '';
                }
                this.title = '设备编号:' + row.val.deviceNo + '，设备状态:' + state;
                this.$refs.detail.show({
                    hasClose: true,
                    action:{
                        cancel: function () {
                            that.isDialogTable = false;
                            that.isLoading = false;
                        }
                    }
                });
                this.dialogTable.queryParam.chargingPlieGuid = row.val.chargingPlieGuid;
                this.dialogTable.queryParam.chargingGuid = row.val.chargingGuid;
                this.isDialogTable = true;
                this.getLineData(row.val);
            },
          
        },
        computed: {
          commInfo(){
             return [
                {text: '设备编号', value: this.row.deviceNo || '--'},
                {text: '安装地址', value: this.row.installAddr || '--'},
                {text: '安装日期', value: this.row.installDate || '--'}
             ]
          }
        },
        created: function () {
          this.tableOpt.queryParam.chargingPlieGuid =  this.searchParam.chargingPlieGuid =  this.row.chargingPlieGuid;
          this.tableOpt.queryParam.deviceNo = this.searchParam.deviceNo = this.row.deviceNo;
          this.tableOpt.queryParam.startDate =this.searchParam.startDate =  this.searchParams.startDate;
          this.tableOpt.queryParam.endDate = this.searchParam.endDate = this.searchParams.endDate;
          this.tableShow = true
          //this.getDeviceInfo();
        },
        mounted: function () {
           
        }
    }
</script>
<style lang="scss" scoped>
  @import "../../../assets/main.scss";
  $commInfoHs: 12em;
  .topInfoCon{
    padding:0 1em !important;
    height:commInfoHs;
    .list-content{
      padding:0.3em 0.5em !important;
    }
    display: flex;
    .commInfoLs{
      flex: 3;
      position:relative;
      height:100%;
      padding-left:2em!important;
      margin-left:2em;
      margin-top:0!important;
      .accountCon{
        height:calc(#{$commInfoHs} - 2em);
        display:flex;
        justify-content: space-around;
        .oneInFours{
           /*flex: 1;*/
           width: 25%;
           height:calc(100% - 2em);
           /*float:left;*/
           margin: 2em 0.5em 0 0.5em;
           div.contentES{
             display:table;
             width:100%;
             background: url('../../../../dist/img/widsomApp/usage-count.png') no-repeat;
             height: 4.75em;
             background-position: center;
             background-size: contain;
             width: 16em;
             background-size: 100% 100%;
             div.analyseTypeS{
               padding-left:28%;
               display: table-cell;
               vertical-align: middle;
               text-align: left;
                width: -webkit-fit-content;
                width: -moz-fit-content;
                width: fit-content;
               div{
                   display: table-cell;
                   vertical-align: middle;
                   height: 100%;
                  // float: left;
                  font-size:0.87em;
                  //margin-top: 6%;
               }
               div.bigSp{
                 font-size: 2em;
                 padding:0 0.1em 0 0.4em;
               }
               div.bigSp+div{
                 font-size: 0.8em;
                 padding-top:12%
               }
             }
          }
          div.BY{
            background-image: url('../../../../dist/img/widsomApp/electricity.png');
          }
          div.BO{
            background-image: url('../../../../dist/img/widsomApp/electricity_rate.png');
          }
          div.BG{
            background-image: url('../../../../dist/img/widsomApp/gains.png');
          }
        }
      }

    }
    .commInfoLs:before{
        content: '';
        position:absolute;
        display:block;
        height:76%;
        left: 0;
        top: 12%;
        width:1px;
        background: rgba(255,255,255,0.3);
    }
    .commInfoLs:after{
        @extend .clearElement
    }
    .commInfoRs{
        flex: 1;
        font-size:1.05em;
        height: $commInfoHs;
        .form-list{
           margin:1em 0.5em;
        }
      }
    }

    .bottomList{
      height:calc(100% - 14em);
      .chartCon{
          display:flex;
          height:calc(100% - 3em);
         .oneInThree{
             flex: 1;
             height:100%;
             margin:0 0.8em;
             .chart{
                height: calc(100% - 3em);

                width:100%
             }
             p{
               font-size: 0.9em;
               width: -webkit-fit-content;
               width: -moz-fit-content;
               width: fit-content;
               margin: auto;
             }
          }
      }

    }
  .costAccount-content{
      width: 100%;
      .mychar-box{
          width: 100%;
          height: 15em;
          display: flex;
          justify-content: space-around;
          &>div{
              height: 15em;
              width: 33%;
          }
      }
  }
</style>



