<template>
    <div ref="trys"  class="info-div">
    	<div v-if="showInfo" class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" ref="infoDetail" :searchParams="searchParam"></info>
        </div>
    	<div   v-show="!showInfo" class="info-container" style="height:100%;margin-top:0">
    		<div class="list-content  list-contentB" style="padding:0 0.5em 1em 0.5em">
    			<div class="list-header noborder-ListHeader">
   	  		      <i class="hasIcon hasImg">
                    <img src="dist/img/widsomApp/data_statistics.png" />
                  </i>
   	  	     	  统计图表
   	  	        </div>
   	  	        <div class="operate-div">
   	  	        	<datePicker ref="timer"  style="float:right;margin-right:1.5em" :dateOpt="dateOpt" @getDatePicker="getFrom"></datePicker> 
   	  	        </div>
   	  	        <div class="topChartCon">
   	  	           <div class="lLineChart" ref="lineChart"></div>
   	  	           <div class="rightAnalyse">
                   
   	  	           	 <div class="oneInThree">
   	  	           	 	<span class="bigSpan b">{{analyseData.meterUsePower}}</span>
   	  	           	 	<span class="b" style="font-size:1em">KWH</span>
   	  	           	 	<span class="hasImgs">
   	  	           	 		<i class="hasIcon hasImg"><img src="dist/img/widsomApp/electric.png" /></i>
   	  	           	 	     <font class="b">{{lineSearchDate !== '' ? parseInt(lineSearchDate.split('-')[1]) : ''}}</font>月累计电量
   	  	           	 	</span>
   	  	           	 </div>
                      <div class="oneInThree">
                      <span class="bigSpan b">{{analyseData.deductMoneyCount}}</span>
                      <span class="b">元</span>
                      <span class="hasImgs">
                        <i class="hasIcon hasImg"><img src="dist/img/widsomApp/electric.png" /></i>
                           <font class="b">{{lineSearchDate !== '' ? parseInt(lineSearchDate.split('-')[1]) : ''}}</font>月累计营收
                      </span>
                     </div>
   	  	           	 <div class="oneInThree">
   	  	           	    <span class="bigSpan o">{{analyseData.meterUseMoney}}</span>
   	  	           	 	<span class="o">元</span>
   	  	           	 	<span class="hasImgs">
   	  	           	 		<i class="hasIcon hasImg"><img src="dist/img/widsomApp/power_rate.png" /></i>
   	  	           	 	    <font class="o">{{lineSearchDate !== '' ? parseInt(lineSearchDate.split('-')[1]) : ''}}</font>月累计电费
   	  	           	 	</span>
   	  	           	 </div>

   	  	           	 <div class="oneInThree">
   	  	           	 	<span class="bigSpan o">{{analyseData.meterProfit}}</span>
   	  	           	 	<span class="o">元</span>
   	  	           	 	<span class="hasImgs">
   	  	           	 		<i class="hasIcon hasImg"><img src="dist/img/widsomApp/power_rate.png" /></i>
   	  	           	 	     <font class="o">{{lineSearchDate !== '' ? parseInt(lineSearchDate.split('-')[1]) : ''}}</font>月盈利
   	  	           	 	</span>
   	  	           	 </div>
   	  	           </div>
   	  	        </div>
    		</div>
    		<div class="list-content  list-contentB" style="padding:0 0.5em 1em 0.5em;height:calc(100% - 20em)">
    			<div class="list-header noborder-ListHeader">
   	  		      <i class="hasIcon hasImg">
                    <img src="dist/img/widsomApp/table_list.png" />
                  </i>
   	  	     	  电量电费统计列表
   	  	        </div>
   	  	        <div class="operate-div">
   	  	          <template v-for="(item, $index) in formList">
                    <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options" @getInput="getFrom" style="margin-right:1.5em"></inputs>
                    <datePicker ref="timer" style="margin-left:10px" v-if="item.type == 'timer'"    type= "doubleDate"  :dateOpt="item.options"
                       @getDatePicker="getFrom"></datePicker>
                  </template>
   	  	        </div>
   	  	        <div ref="myOwnTable" class="table-container" style="padding:0 1em;width:calc(100% - 2em);height:calc(100% - 3.5em)" >
                 <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" @rowDbClick="checkInfo"></tables>
                </div>
    		</div>
    	</div>
    </div>
</template>
<script>
  import info from './info.vue'
  export default{
    data() {
      return {
      	controls: [],
      	lineSearchDate: '',
      	row: '',
      	showInfo: false,
        dateOpt:  {
            key: 'lineSearchDate',
            width: '16em',
            onlyMonth: true
        },
        lineChartDom: '',
        analyseData: {
          deductMoneyCount: '--',
        	meterUsePower: '--',
        	meterUseMoney: '--',
        	meterProfit: '--'
        },
        searchParam: {
        	startDate: '',
        	endDate: '',
        	deviceNo: '',
            commNo: ''
        },
        searchDate: '',
        tableShow:false,
        tableOpt: {
            column: [
              {title: '设备编号', field: 'deviceNo',textAlign: 'center',width: '160'},
              {title: '通信编号', field: 'commNo',textAlign: 'center',width: '160'},
              {title: '安装地址', field: 'installAddr', textAlign: 'center', width: '250'},
              {title: '使用次数', field: 'useNumber', textAlign: 'center', width: '160'},
              {title: '电量（KWH）', field: 'electricQuantity', textAlign: 'center', width: '160'},
              {title: '电费（元）', field: 'electricityFees', textAlign: 'center', width: '160'}
            ],
            url: 'charger/electricCountList',
            method: 'post',
            // method: 'get',
            // url: 'dist/json/elecCost.json',
            pageSize: 20,
            pageNumber: 1,
            pageList: [20, 40, 60],
            queryParam: {},
            isExport: true,
            exportURL: 'charger/electricCountListDown',
            showTotal: true,
            dataRow: 'electricAndFeeDtoList',
            totalOpt: [
                {title: '电量（KWH）', field: 'electricQuantity', textAlign: 'center', width: '80'},
                {title: '电费（元）', field: 'electricityFees', textAlign: 'center', width: '80'}
            ]
        }
      }
    },
    components: {
         info
    },
    computed: {
      formList: function () {
     		let dates = this.initDate(true);
            return [
               {
                 type: 'timer',
                 options: {
                   key: 'searchDate',
                   value: {startDate: dates.start, endDate: dates.end},
                   width: '23em',
                   fontSize: '0.8em'
                 }
               },
               {
                 type: 'input',
                 options: {
                   type: 'typeSearch',
                   key: 'searchParam',
                   selectOpt: {
                     width: '7em',
                     cs: 'client',
                     data: [
                      {text: '设备编号', value: 'deviceNo'},
                      {text: '通信编号', value: 'commNo'},
                     ]
                   }
                 }
               }
            ]
     	},
    },
    methods: {
      checkInfo: function (val) {
      	this.row = val.val;
      	this.showInfo = true;
      },
      closeDiv: function () {
      	this.showInfo = false;
      	this.resizeCon();
      },
      getParams: function (pageNumber) {
          let arr = this.getSearchData(this.searchParam);
          this.getHeight('calc(100% - 3.5em)');
          this.searchTable(arr, pageNumber);
      },
      getFrom: function (val) {
     	  if (val.combine) {
              if ( this.searchParam[val.key.split('.')[1]] !== val.val) {
                this.searchParam = this.cleanSearchData(this.searchParam, ['startDate', 'endDate']);
                this.getOption(val, true);
                this.getParams(1);
              }
            } else {
              this.getOption(val, true);
              if(val.key == 'searchDate')  {
                let arr = val.val.split('至');
                this.searchParam.startDate = arr[0];
     	        this.searchParam.endDate = arr[1];
              }	
              if(val.key == 'lineSearchDate') {
                  this.lineChart();
              } else  {
              	  this.getParams(1);
              }            
            }
      },
      lineChart: function () {
         let that = this;
         let option = {
         	color: ['#207BDA', '#EB9B3F'],
            tooltip: {
            	trigger: 'axis',
            	axisPointer: {
            		type: 'cross',
            		crossStyle: {
            			color: '#999'
            		}
            	}
            },
          
            grid: {
                left: 50,
                right: 40,
                bottom:20,
                top: 40
            },
            legend: {
                data:['电量','电费'],
                borderRadius: 11,
                icon: 'roundRect',
                itemGap:70,
                itemWidth: 35,
                itemHeight: 11,
                textStyle : {
                	 color: 'rgba(255,255,255, 0.9)'
                }
            },
            xAxis: [
              {
            	type: 'category',
            	data: [],
            	axisPointer: {
            		type: 'shadow'
            	},
            	nameTextStyle: {
                  color: 'rgba(255,255,255, 0.9)',
                },
                axisLabel: {
                	color: 'rgba(255,255,255, 0.9)',
                	textStyle: {
                		color: 'rgba(255,255,255, 0.9)',
                	}
                },
                splitLine: {
                	show:false,
                	lineStyle: {
                		color: 'rgba(255,255,255, 0.2)'
                	}
                },
                axisLine: {
                    show:false,
                    lineStyle: {
                      color: 'rgba(255,255,255, 0.2)'
                    }
                }
              }
           ],
           yAxis: [
            {
            	type: 'value',
            	name: '电量 (KWH)',
            	min: 0,
            	axisLabel: {
            		formatter: '{value}',
            	    color: 'rgba(255,255,255, 0.9)',
            	    textStyle: {
            	    	color: 'rgba(255,255,255, 0.9)'
            	    }
            	},
            	nameTextStyle: {
                    color: 'rgba(255,255,255, 0.9)',
                },     
                splitLine: {
                	show:true,
                	lineStyle: {
                		color: 'rgba(255,255,255, 0.2)'
                	}
                },
                axisLine: {
                    show:false,
                    lineStyle: {
                      color: 'rgba(255,255,255, 0.2)'
                    }
                }
            },
            {
            	type: 'value',
            	name: '电费 (元)',
            	min: 0,
            	axisLabel: {
            		formatter: '{value}',
            	    color: 'rgba(255,255,255, 0.9)',
            	    textStyle: {
            	    	color: 'rgba(255,255,255, 0.9)',
            	    }
            	},
            	nameTextStyle: {
                    color: 'rgba(255,255,255, 0.9)',
                },     
                splitLine: {
                	show:true,
                	lineStyle: {
                		color: 'rgba(255,255,255, 0.2)'
                	}
                },
                axisLine: {
                    show:false,
                    lineStyle: {
                      color: 'rgba(255,255,255, 0.2)'
                    }
                }
            }
           ],
           series: [
              {
               name:'电量',
               type:'bar',
               data:[],
               itemStyle: {
               	 barBorderRadius:  [4,4,0,0]
               }
              },
              {
               name:'电费',
               type:'bar',
               data:[],
               itemStyle: {
               	 barBorderRadius: [4,4,0,0]
               }
              }
            ]
          };
         that.lineChartDom = echarts.init(that.$refs.lineChart);
         that.getRequest({
           url: 'charger/electricCount',
           method: 'post',
         //	url: 'dist/json/chartLine.json',
         //	method: 'get',
         	param: {mark: that.lineSearchDate, model:1},
         	success: function (data) {
               let Data = data.data;
               option.series[0].data = Data.yQuantityData;
               option.series[1].data = Data.yFeesData;
               option.xAxis[0].data = Data.xData;
               that.lineChartDom.setOption(option, true);
               for(let key in that.analyseData) {
               	 that.analyseData[key] = data.data[key]
               }
         	}
         })
      },
      resizeCon: function () {
      	let that = this;
      	setTimeout(function () {
    	  	  	that.lineChartDom != '' ? that.lineChartDom.resize(): '';
              that.getHeight();
    	  },1)	
      	
      	if(this.$refs.infoDetail) {
      		let info = this.$refs.infoDetail;
            if(info.$refs.myTable) {
               info.$refs.myTable.onResize()
            }
      	}
      },
       windowChange: function () {
          this.resizeCon();
          //let that = this;
          // window.onresize = function () {
          //   that.resizeCon();
          // }
       },
      init: function () {
        this.tableOpt.isExport = this.controls[6];
        this.tableShow = true;
        //let that = this;
        // window.onresize = function () {
        //   that.resizeCon();
        // }
      }
    },
    mounted: function() {
    
    },
    created: function () {
      this.isIn();
      this.getAction();
    	let dates = this.initDate(true);
      this.tableOpt.queryParam['startDate'] = this.searchParam.startDate = dates.start;
      this.tableOpt.queryParam['endDate'] = this.searchParam.endDate = dates.end;
      //this.tableShow = true;
    }
  }
</script>
<style lang="scss">
$lineHeight: 13em;
.list-contentB{
    .topChartCon{
    	 height:$lineHeight;
    	 display: flex;
    	 width:100%;
       .lLineChart{
          height:100%;
          flex:5;
       }
       .rightAnalyse{
           height:100%;
           flex:3;
           display: flex;
           div.oneInThree{
              flex: 1;
              height: calc(100% - 5em);
              margin: 3.5em 0.2em 1em 0.2em;
              background: rgba(0,0,0,0.35);
              span{
              	display: block;
              	width:100%;
              	margin-top: 0.8em;
              	font-size: 0.87em;
              }
              span.hasImgs{
              	 width: -webkit-fit-content;
                 width: -moz-fit-content;
                 width: fit-content; 
                 margin: 0.9em auto 0 auto 
              }
              span.bigSpan{
              	 font-weight: 600;
              	 font-size: 1.5em;
              }
              .b{
              	color: #247CDB
              }
              .o{
              	color: #EE9C3C
              }
           }
        }
	}
}
  
</style>


